/**
 *   api
 *  @module code/api/user
 *  @author coder
 */

// API_HOST
import {
  API_HOST
} from '@/constant'

// ajax
import ajax from '$ui/utils/ajax'

/**
 * user
 * @const
 * @type {String}
 * @default  '[API_HOST]/api/user'
 */
export const FETCH_USER = API_HOST + '/api/user'

/**
 * user
 * @param {object} [data] 
 * @param {object} [options] ajax
 * @returns {Promise}
 */
export function fetchUser(data, options) {
  return ajax({
    method: 'get',
    data: data,
    url: FETCH_USER,
    ...options
  })
}

/**
 * user
 * @const
 * @type {String}
 * @default  '[API_HOST]/api/user/:id'
 */
export const GET_USER = API_HOST + '/api/user/:id'

/**
 * user
 * @param {string|number} id Urlid
 * @param {object} [data] 
 * @param {object} [options] ajax
 * @returns {Promise}
 */
export function getUser(id, data, options) {
  return ajax({
    method: 'get',
    params: {
      id
    },
    data: data,
    url: GET_USER,
    ...options
  })
}

/**
 * user
 * @const
 * @type {String}
 * @default  '[API_HOST]/api/user'
 */
export const ADD_USER = API_HOST + '/api/user'

/**
 * user
 * @param {object} [data] 
 * @param {object} [options] ajax
 * @returns {Promise}
 */
export function addUser(data, options) {
  return ajax({
    method: 'post',
    data: data,
    url: ADD_USER,
    ...options
  })
}

/**
 * user
 * @const
 * @type {String}
 * @default  '[API_HOST]/api/user'
 */
export const UPDATE_USER = API_HOST + '/api/user'

/**
 * user
 * @param {object} [data] 
 * @param {object} [options] ajax
 * @returns {Promise}
 */
export function updateUser(data, options) {
  return ajax({
    method: 'patch',
    data: data,
    url: UPDATE_USER,
    ...options
  })
}

/**
 * user
 * @const
 * @type {String}
 * @default  '[API_HOST]/api/user/:id'
 */
export const REMOVE_USER = API_HOST + '/api/user/:id'

/**
 * user
 * @param {string|number} id Urlid
 * @param {object} [data] 
 * @param {object} [options] ajax
 * @returns {Promise}
 */
export function removeUser(id, data, options) {
  return ajax({
    method: 'delete',
    params: {
      id
    },
    data: data,
    url: REMOVE_USER,
    ...options
  })
}

/**
 * user
 * @const
 * @type {String}
 * @default  '[API_HOST]/api/user/remove'
 */
export const BATCH_USER = API_HOST + '/api/user/remove'

/**
 * user
 * @param {object} [data] 
 * @param {object} [options] ajax
 * @returns {Promise}
 */
export function batchUser(data, options) {
  return ajax({
    method: 'post',
    data: data,
    url: BATCH_USER,
    ...options
  })
}

/**
 * user GET_DATA
 * @const
 * @type {String}
 * @default  '[API_HOST]/data.json'
 */
export const GET_DATA = API_HOST + '/data.json'

/**
 * user GET_DATA
 * @param {object} [data] 
 * @param {object} [options] ajax
 * @returns {Promise}
 */
export function getData(data, options) {
  return ajax({
    method: 'get',
    responseType: 'blob',
    headers: {
      "Content-type": "application/x-www-form-urlencoded"
    },
    data: data,
    url: GET_DATA,
    ...options
  })
}
