/**
 *  .my/core/build/routes-builder-plugin.js webpack
 */
export default function ({
  get
}) {
  return [{
    path: '',
    component: get('index'),
    meta: null,
    props: true
  }, {
    path: '/main',
    component: get('main/index'),
    meta: null,
    props: true
  }, {
    path: '/theme',
    component: get('theme/_layout'),
    children: [{
      path: '/theme',
      component: get('theme/index'),
      meta: null,
      props: true
    }, {
      path: '/theme/pro',
      component: get('theme/pro'),
      meta: null,
      props: true
    }, {
      path: '/theme/ui',
      component: get('theme/ui'),
      meta: null,
      props: true
    }],
    meta: null,
    props: true
  }, {
    path: '/*',
    component: get('_404'),
    meta: null,
    props: true
  }]
}
